<template>
    <div class="main_app">
        <div v-if="showType==='0'" style="overflow: hidden;width: 100%;height: 100%;background-color: #1A1A1A">
            <el-image style="width: 200px;height: 200px" :src="require('@/assets/login.png')"></el-image>
            <h1>Hello PetPlugin</h1>
            <div>
                <el-input
                        placeholder="请输入用户名"
                        style="width: 80%;height: 30px"
                        v-model="userName"
                        clearable>
                    <i slot="prefix" class="el-input__icon el-icon-user-solid"></i>
                </el-input>
            </div>
            <div style="margin-top: 20px">
                <el-input placeholder="请输入密码"
                          v-model="password"
                          style="width: 80%;height: 30px"
                          show-password
                          clearable>
                    <i slot="prefix" class="el-input__icon el-icon-lock"></i>
                </el-input>
            </div>
            <div style="margin-top: 20px">
                <el-button type="primary" style="width: 80%" @click="loginPet"><span
                        style="font-weight: bold">LOGIN</span></el-button>
            </div>
            <div style="margin-top: 15px;">
                <div style="width: 100%;display: flex;justify-content: center">
                    <div style="width: 80%">
                        <div style="float: left" v-if="this.userName&&this.password">
                            <el-checkbox v-model="rememberFlag" style="color: #ffffff;font-size: 13px;" @change="rememberInfo">记住用户名/密码</el-checkbox>
                        </div>
                        <div class="code_world" @click="getCode('1')" style="float: right">不知道密码？点这里</div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="showType==='1'" style="overflow: hidden;width: 100%;height: 100%;background-color: #1A1A1A">
            <div style="margin-top: 30px">
                <el-image style="width: 200px;height: 200px" :src="require('@/assets/aiPetQrCode.png')"></el-image>
            </div>
            <div style="width: 80%;margin: auto">
                <h1 style="color: #ffffff">Hello,小伙伴~</h1>
                <div style="color: #666666;font-size: 15px">
                    我是PetPlugin,是ChatPet的Chrome插件哦，
                    希望我的出现能为您带来便捷，目前我是以<span style="color: #3076F6">GPT-3.5</span>作为AI内核。
                </div>
                <div style="color: #ffffff;font-size: 15px">
                    关注公众号，或者后台回复:'<span style="color: #E1251B">chatpet</span>'，领取账号，快向我提问吧~
                </div>
            </div>
            <div style="margin-top: 20px;">
                <span class="code_world" @click="getCode('0')">我已关注，立即体验</span>
            </div>
        </div>
        <div v-if="showType==='2'" style="overflow: hidden">
            <chat-content></chat-content>
        </div>
    </div>
</template>

<script>
    import ChatContent from "@/popup/App/component/chatContent";

    export default {
        name: 'app',
        components: {ChatContent},
        data() {
            return {
                userName: '',
                password: '',
                showType: '0',
                rememberFlag: false,
            }
        },
        mounted() {
            this.judgeLogin();
        },
        methods: {
            getCode(param) {
                this.showType = param;
            },
            loginPet() {
                if(!this.userName){
                    this.$message.error('用户名不能为空');
                    return
                }

                if(!this.password){
                    this.$message.error('密码不能为空');
                    return
                }

                if (this.userName === 'chatpet' && this.password === 'chatpet666') {
                    this.showType = '2';
                    chrome.storage.sync.set({'loginType': '2'});
                } else {
                    this.showType = '1';
                }
            },
            judgeLogin() {
                chrome.storage.sync.get('userName',(result)=>{
                    if(result.userName){
                        this.userName=result.userName;
                    }
                })

                chrome.storage.sync.get('password',(result)=>{
                    if(result.password){
                        this.password=result.password;
                    }
                })

                chrome.storage.sync.get('rememberFlag',(result)=>{
                    if(result.rememberFlag){
                        this.rememberFlag=result.rememberFlag;
                    }else {
                        this.rememberFlag=false;
                        chrome.storage.sync.set({'rememberFlag': this.rememberFlag});
                    }
                })

                chrome.storage.sync.get('loginType', (result) => {
                    if(result.loginType){
                      this.showType = result.loginType
                    }else{
                      this.showType = '0';
                      chrome.storage.sync.set({'loginType': '0'});
                    }
                });
            },
            rememberInfo(){
                chrome.storage.sync.set({'rememberFlag': this.rememberFlag});
                if(this.rememberFlag){
                    chrome.storage.sync.set({'userName': this.userName});
                    chrome.storage.sync.set({'password': this.password});
                }else{
                    chrome.storage.sync.set({'userName': ''});
                    chrome.storage.sync.set({'password': ''});
                }
            }
        }
    }
</script>

<style>
    .main_app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        width: 350px;
        height: 500px;
        /*background-color: #1A1A1A;*/
        color: #ffffff;
    }

    .code_world {
        color: #ffffff;
        font-size: 13px;
        text-align: center;
    }

    .code_world:hover {
        color: #3076F6;

    }
</style>
