<template>
    <div class="tool-container">
      <div style="width: 95%">
        <div style="height: 30px;margin-top: 10px;">
          <div style="display: flex;align-items: center;float: left">
            <el-image
                    style="width: 30px; height: 30px;"
                    :src="titlePic"
                    fit="fit"></el-image>
            <div style="color: #1A1A1A;margin-left: 10px;font-weight: bold;font-size: 18px">PETPLUGIN</div>
          </div>
          <div style="float: right;display: flex;align-items: center;height: 100%">
            <i class="el-icon-switch-button" style="color: #3076F6;font-size: 20px;font-weight: bold;margin-right: 10px" @click="closePlugin"></i>
          </div>
        </div>
        <div>
          <el-divider></el-divider>
        </div>
        <div>
          <div style="width: 100%;display:flex;">
            <div style="width:50%">
              <div style="float: left;font-size: 15px;font-weight: bold;color: #1A1A1A">PetPlugin Switch</div>
            </div>
            <div style="width:50%">
              <div style="float: right;">
                <el-switch
                        v-model="showPetPluginSwitch"
                        @change="changeShow"
                        active-color="#13ce66">
                </el-switch>
              </div>
            </div>
          </div>
        </div>

        <div style="width: 100%;height: 390px;margin-top: 20px;">
          <div v-if="showPetPluginSwitch">
            <div style="display: flex">
              <el-button  round size="mini" style="width: 25%;" v-for="(item,index) in buttonTypeList" :key="index" :type="item.type" @click="changeSelectType(item)">
                {{item.name}}
              </el-button>
            </div>
            <div style="margin-top: 20px">
              <el-divider>
                <i class="el-icon-setting" v-if="selectType===0"></i>
                <i class="el-icon-document-copy" v-if="selectType===1"><span style="color: #666666;margin-left: 10px">{{chatPluginList.length}}</span></i>
                <i class="el-icon-monitor" v-if="selectType===2"></i>
                <i class="el-icon-chat-line-square" v-if="selectType===3"></i>
              </el-divider>
            </div>
            <div style="margin-top: 20px" v-if="selectType===0">
              <div style="font-weight: bold;font-size: 13px;">
                <div style="float: left;">字体大小</div>
                <div style="float: right;"><span style="color: #3076F6">{{fontSize}}</span>px</div>
              </div>
              <div style="width: 100%;display: flex;justify-content: center;">
                <el-slider v-model="fontSize" :min="10" :max="20" style="width: 96%"></el-slider>
              </div>
              <div style="font-weight: bold;font-size: 13px;">
                <div style="text-align: left">主题选择</div>
              </div>
              <div style="width: 100%;display: flex;margin-top: 15px">
                <el-input
                        placeholder="请输入内容"
                        v-model="colorType"
                        style="float: left;width: 85%"
                        size="mini"
                        class="plugin_color_type"
                        clearable>
                </el-input>
                <div style="width: 15%">
                  <el-color-picker v-model="colorType" style="float: right" size="mini"></el-color-picker>
                </div>
              </div>
              <div style="font-weight: bold;font-size: 13px;margin-top: 15px;">
                <div style="text-align: left">AI内核</div>
              </div>
              <div style="width: 100%;display: flex">
                <div>
                  <div v-for="(item,index) in aiList" :key="index" style="margin-top: 20px;float: left;width: 50%">
                    <el-radio v-model="radioValue" :label="item.label" border class="plugin_radio_item">
                      {{item.name}}
                    </el-radio>
                  </div>
                </div>
              </div>
              <div style="font-weight: bold;font-size: 13px;margin-top: 20px;">
                <el-button type="primary" round style="float: right;margin-left: 10px" size="mini" @click="moreSetting">更多详情</el-button>
                <el-button type="primary" round style="float: right;margin-left: 10px" size="mini" @click="goToChatPet">ChatPet</el-button>
                <el-button type="info" round style="float: right" size="mini" @click="reSet">重置</el-button>
              </div>
            </div>
            <div style="margin-top: 20px" v-if="selectType===1">
              <div style="font-weight: bold;font-size: 13px;">
                <div style="margin-top: 15px;display: flex">
                  <el-button type="danger" round size="mini" @click="deleteChatList"><i class="el-icon-delete"></i></el-button>
                  <el-button type="success" round size="mini" style="margin-left: 10px" @click="addChatType"><i class="el-icon-plus"></i></el-button>
                  <el-input placeholder="请输入Chat名称" v-model="searchContent" class="input-with-select" size="mini" style="margin-left: 10px">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                </div>
              </div>
              <div style="height: 280px;margin-top: 10px;overflow: auto">
                <div style="height: 25%;width: 100%;display: flex;justify-content: center;align-items: center;" v-for="(item,index) in chatPluginList" :key="index">
                  <div class="plugin_chat_item" :style="{borderColor:item.selectFlag?'#3076F6':'#DCDFE6',color:item.selectFlag?'#3076F6':'#DCDFE6'}">
                    <div style="width: 20%;display: flex;justify-content: center">
                      <el-avatar :src="item.titlePic"></el-avatar>
                    </div>
                    <div style="width: 50%">
                      <div style="font-size: 16px;font-weight: bold;display: flex" v-if="item.editFlag">
                        <div style="width: 90%">
                          <div style="float: left;">
                            <el-input
                                    placeholder="请输入内容"
                                    v-model="item.titleName"
                                    class="item_input"
                                    size="small"
                                    maxlength="8"
                                    show-word-limit
                                    clearable>
                            </el-input>
                          </div>
                        </div>
                        <div style="width: 10%;display: flex;align-items: center">
                          <div style="float:right;">
                            <i class="el-icon-document-checked" @click="item.editFlag=false"></i>
                          </div>
                        </div>
                      </div>
                      <div style="font-size: 16px;font-weight: bold;" v-else>
                        <div style="float: left">
                          {{item.titleName}}
                        </div>
                        <div style="float:right;">
                          <i class="el-icon-edit-outline" @click="item.editFlag=true"></i>
                          <i class="el-icon-delete" style="margin-left: 10px" @click="deleteChat(item)"></i>
                        </div>
                      </div>
                    </div>
                    <div style="width: 30%;display: flex;align-items: center;justify-content: center;">
                      <el-button type="primary" round style="width: 60%" size="mini" @click="chooseChat(item)">GO</el-button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div style="margin-top: 20px" v-if="selectType===2">
              <div style="height: 310px">
                <div class="plugin_type_item" v-for="(item,index) in pluginType" :key="index">
                  <div class="plugin_type_item_inner">
                    <div style="width: 100%">
                      <el-image
                              style="width: 100%; height: 100%;border-radius: 10px;filter: blur(1px);"
                              :src="item.titlePic"
                              fit="cover"></el-image>
                    </div>
                    <div style="width: 0;height: 0">
                      <div style="position: absolute;top:0;left:0;">
                        <div style="color: #ffffff;font-family: 华文琥珀;letter-spacing: 2px;font-size: 18px;background-color: #E1251B;padding:2px 5px 2px 5px;border-radius: 10px 0 10px 0">{{item.name}}</div>
                      </div>
                      <div style="position: absolute;top:18px;right:10px;">
                        <el-button type="primary" round size="mini" style="background-color: blueviolet;border: none" @click="addChat(item)" v-if="item.developFlag">CREATE</el-button>
                        <el-button type="primary" round size="mini" style="background-color: blueviolet;border: none" @click="addChat(item)" v-else disabled>研发中</el-button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div style="margin-top: 20px;position: relative;height: 320px" v-if="selectType===3">
              <div v-if="tagShowType===0">
                <el-input placeholder="请输入prompt" v-model="tagSearchContent" size="mini">
                  <el-select v-model="searchTagType" slot="prepend" placeholder="# Tag" style="width: 80px">
                    <el-option :label="item.tagName" :value="item.label" v-for="(item,index) in tagList" :key="index"></el-option>
                  </el-select>
                  <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
                <div style="height: 250px;overflow: auto;">
                  <div v-for="(item,index) in tagItems" :key="index" style="width: 100%;height: 20%;display: flex;align-items: center;justify-content: center">
                    <div class="tag_item" :style="{backgroundColor:item.color}" @click="useTag(item)">
                      <div class="tag_title">
                        {{item.tagType}}
                      </div>
                      <div style="width: 55%;display: flex;justify-content: left;align-items: center;font-weight: bold;font-size: 14px">
                        {{item.tagName}}
                      </div>
                      <div style="width: 25%;display: flex;justify-content: center;align-items:center;font-size: 15px;">
                        <i class="el-icon-edit-outline" @click.stop="editTag(item)"></i>
                        <i class="el-icon-delete" style="margin-left: 10px" @click.stop="deleteTag(item)"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else>
                <div style="width: 100%;height: 280px;font-weight: bold;font-size: 13px;">
                  <div v-if="tagClassFlag">
                    <div>
                      <div style="text-align: left" @click="tagClassFlag=!tagClassFlag">Prompt类型/自定义类型</div>
                    </div>
                    <div style="margin-top: 15px">
                      <el-select v-model="selectCreateContent" clearable placeholder="请选择" size="mini" style="width: 100%" :disabled="addTagType===1" filterable clearable>
                        <el-option
                            v-for="(item,index) in tempTagList"
                            :key="index"
                            :label="item.tagName"
                            :value="item.label">
                        </el-option>
                      </el-select>
                    </div>
                  </div>
                  <div v-else>
                    <div>
                      <div style="text-align: left" @click="tagClassFlag=!tagClassFlag" >自定义类型/Prompt类型</div>
                    </div>
                    <div style="margin-top: 15px;display: flex">
                      <el-input
                          placeholder="请输入内容"
                          v-model="inputCreateContent"
                          size="mini"
                          style="width: 85%"
                          maxlength="3"
                          show-word-limit
                          :disabled="addTagType===1"
                          clearable>
                      </el-input>
                      <div style="width: 15%">
                        <el-color-picker v-model="tempColor" size="mini" style="float: right" :disabled="colorSelectFlag"></el-color-picker>
                      </div>
                    </div>
                  </div>
                  <div style="margin-top: 15px">
                    <div style="text-align: left">Prompt标题</div>
                  </div>
                  <div style="margin-top: 15px">
                    <el-input
                        placeholder="请输入内容"
                        v-model="promptTitle"
                        size="mini"
                        maxlength="10"
                        show-word-limit
                        clearable>
                    </el-input>
                  </div>
                  <div style="margin-top: 15px">
                    <div style="text-align: left">Prompt内容</div>
                  </div>
                  <div style="margin-top: 15px">
                    <el-input
                        type="textarea"
                        :rows="4"
                        style="font-family: 微软雅黑;font-size: 15px"
                        placeholder="请输入Prompt"
                        v-model="promptContent">
                    </el-input>
                  </div>
                </div>
              </div>
              <div style="position:absolute;bottom:0;right:0;height: 40px;line-height: 40px">
                <div style="float: right" v-if="tagShowType===0">
                  <el-button type="danger" size="small" round @click="deleteTagList"><i class="el-icon-delete"></i></el-button>
                  <el-button type="success" size="small" round style="margin-left: 10px" @click="downloadTag"><i class="el-icon-download"></i></el-button>
                  <el-button type="warning" size="small" round style="margin-left: 10px" @click="createTag"><i class="el-icon-s-opportunity"></i></el-button>
                </div>
                <div style="float: right" v-else>
                  <el-button type="danger" size="small" round @click="deleteTemp"><i class="el-icon-delete"></i></el-button>
                  <el-button type="info" size="small" round style="margin-left: 10px" @click="closeTag"><i class="el-icon-close"></i></el-button>
                  <el-button type="success" size="small" round style="margin-left: 10px" @click="addTag"><i class="el-icon-check"></i></el-button>
                </div>
              </div>
            </div>
          </div>
          <div v-else>
            <div style="display: flex">
              <el-button  round size="mini" style="width: 25%;" v-for="(item,index) in buttonTypeList" :key="index" type="info" plain :disabled="true">
                {{item.name}}
              </el-button>
            </div>
            <div style="display: flex;justify-content: center;align-items: center;height: 300px">
              <div>
                <el-image
                        style="width: 180px; height: 180px"
                        :src="unloadPic"
                        fit="fit"></el-image>
                <div style="font-size: 15px;color: #C8C9CC;font-weight: bold">
                  <div>请先开启PetPlugin Switch，才可以操作哦~</div>
                  <div style="margin-top: 10px">Please Open The PetPlugin Switch To Operate It~</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import download from "downloadjs";

    export default {
        name: "chatContent",
      data(){
          return{
            titlePic:require('@/assets/login.png'),
            unloadPic:require('@/assets/unloginPic.png'),
            showPetPluginSwitch:false,
            colorSelectFlag:false,
            tempColor:'',
            tempColorId:'',
            colorType:'#3D4554',
            searchContent:'',
            radioValue:'1',
            selectType:1,
            fontSize:15,
            searchTagType:0,
            tagSearchContent:'',
            tagShowType:0,
            addTagType:0,
            selectCreateContent:'',
            inputCreateContent:'',
            promptTitle:'',
            promptContent:'',
            tagClassFlag:true,
            chatPluginList:[],
            tempTagList:[],
            pluginType:[
              {
                titlePic:require('@/assets/petType1.png'),
                name:'AI对话',
                type:'CHAT',
                desc:'',
                developFlag:true,
              },
              {
                titlePic:require('@/assets/petType2.png'),
                name:'AI绘画',
                type:'DRAW',
                desc:'',
                developFlag:false,
              },
              {
                titlePic:require('@/assets/petType3.png'),
                name:'AI编程',
                type:'CODE',
                desc:'',
                developFlag:false,
              },
              {
                titlePic:require('@/assets/petType4.png'),
                name:'AI翻译',
                type:'TRAN',
                desc:'',
                developFlag:false,
              },
            ],
            aiList:[
              {
                name:"GPT-3.5",
                label:'1',
              },
              {
                name:"GPT-4.0",
                label:'2',
              },
              {
                name:"fsChat-1.0",
                label:'3',
              },
              {
                name:"chatBLM-1.0",
                label:'4',
              },
            ],
            buttonTypeList:[
              {
                type:'',
                name:'页面配置',
                select:0,
              },
              {
                type:'primary',
                name:'Chat记录',
                select:1,
              },
              {
                type:'',
                name:'Pet类型',
                select:2,
              },
              {
                type:'',
                name:'提示词',
                select:3,
              },
            ],
            tagList:[
              {
                tagName:'#ALL',
                tagColor:'',
                label:0,
              },
              {
                tagName:'#文案',
                tagColor:'#9B27B3',
                label:1,
              },
              {
                tagName:'#编程',
                tagColor:'#18B0AB',
                label:2,
              },
              {
                tagName:'#提问',
                tagColor:'#520AFB',
                label:3,
              },
              {
                tagName:'#绘画',
                tagColor:'#EF060E',
                label:4,
              },
            ],
            tagItems:[
              {
                tagId:'0',
                label:1,
                tagType:'#文案',
                tagName:'创作一篇好的周报',
                tagContent:'你是一个文案高手,请帮我写一篇1000字的周报,以下是我的工作内容:',
                color:'#9B27B3',
              },
              {
                tagId: '1',
                label:2,
                tagType:'#编程',
                tagName:'帮我优化SQL',
                tagContent:'你是一个编程高手,擅长SQL方向的优化,可以帮我优化以下SQL嘛:',
                color:'#18B0AB',
              },
              {
                tagId: '2',
                label:3,
                tagType:'#提问',
                tagName:'怎么高效地完成任务',
                tagContent:'你是一个高效率的人,请告诉我怎么高效地完成下面的事情,如果不知道就回答不知道,不能乱回答',
                color:'#520AFB',
              },
              {
                tagId: '3',
                label:4,
                tagType:'#绘画',
                tagName:'炫酷科幻的宇宙飞船',
                tagContent:'一艘美丽的飞船,穿梭于浩瀚的宇宙,飞船表面十分光滑,散发着科幻的色彩',
                color:'#EF060E',
              },
            ],
          }
        },
      mounted() {
          this.initInfo();
          this.initChatPluginList();
          this.initTagList();
      },
      methods:{
        // 获取uuid
        getUuid() {
          var s = [];
          var hexDigits = "0123456789abcdef";
          for (var i = 0; i < 36; i++) {
            s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
          }
          s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
          s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
          s[8] = s[13] = s[18] = s[23] = "-";

          var uuid = s.join("");
          return uuid;
        },



        // 初始化ChatPluginList
        initChatPluginList(){
          chrome.storage.sync.get(['chatPluginList','chooseChat'], (result) => {
            if(!result.chatPluginList||result.chatPluginList.length===0){
              this.addChat(this.pluginType[0]);
            }else {
              this.chatPluginList=result.chatPluginList;
              let chatId=this.chatPluginList[0].chatUuid;
              if(result.chooseChat){
                chatId=result.chooseChat;
              }
              for(let i=0;i<this.chatPluginList.length;i++){
                this.chatPluginList[i].selectFlag=false;
                if(this.chatPluginList[i].chatUuid==chatId){
                  this.chatPluginList[i].selectFlag=true;
                }
              }
            }
            chrome.storage.sync.set({ 'chatPluginList': this.chatPluginList });
          });
        },

        // 初始化TagList
        initTagList(){
          chrome.storage.sync.get(['tagList','tagTypeList'], (result) => {
            if(!result.tagList||result.tagList.length===0){
              chrome.storage.sync.set({ 'tagList': this.tagItems });
            }else {
              this.tagItems=result.tagList;
            }

            if(!result.tagTypeList||result.tagTypeList.length===0){
              chrome.storage.sync.set({ 'tagTypeList': this.tagList });
            }else {
              this.tagList=result.tagTypeList;
            }

            if(this.searchTagType!==0){
              this.tagItems=this.tagItems.filter(item=>item.label===this.searchTagType);
            }
          });
        },

        // 选择Chat
        chooseChat(param){
          for(let i=0;i<this.chatPluginList.length;i++){
            this.chatPluginList[i].selectFlag=false
          }
          param.selectFlag=true;
          chrome.storage.sync.set({ 'chooseChat': param.chatUuid });

          //刷新页面
          this.reloadTab();
        },

        // 添加Chat
        addChat(param){
          let uuid=this.getUuid();
          let chatItem={
            chatUuid: uuid,
            titlePic: param.titlePic,
            titleName:param.type+'-'+uuid.slice(0,2),
            editFlag:false,
            selectFlag:true
          }
          this.chatPluginList.unshift(chatItem);
          this.chooseChat(this.chatPluginList[0]);
          chrome.storage.sync.set({ 'chatPluginList': this.chatPluginList });
          //返回chatList页面
          this.buttonTypeList[1].type='primary';
          this.buttonTypeList[2].type='';
          this.selectType=1;
          this.$message({
            message: '添加成功~',
            type: 'success'
          });
        },

        // 添加种类
        addChatType(){
          this.buttonTypeList[1].type='';
          this.buttonTypeList[2].type='primary';
          this.selectType=2;
        },

        // 清空chatPluginList
        deleteChatList(){
          this.chatPluginList=[];
          chrome.storage.sync.set({ 'chatPluginList': this.chatPluginList });
          this.initChatPluginList();
          this.$message({
            message: '清空会话成功~',
            type: 'success'
          });
        },

        // 删除单个
        deleteChat(param){
          if(this.chatPluginList.length===1){
            this.chooseChat(this.chatPluginList[0]);
            this.$message({
              message: '得保持至少一条chat，不能删除哦~',
              type: 'warning'
            });
          }else{
            let num=this.chatPluginList.indexOf(param);
            if(num===0){
              if(this.chatPluginList.length>1){
                this.chooseChat(this.chatPluginList[num+1]);
                this.chatPluginList=this.chatPluginList.filter(item=>item!==param);
                chrome.storage.sync.set({ 'chatPluginList': this.chatPluginList });
              }
            }else{
              this.chooseChat(this.chatPluginList[num-1]);
              this.chatPluginList=this.chatPluginList.filter(item=>item!==param);
              chrome.storage.sync.set({ 'chatPluginList': this.chatPluginList });
            }
            this.$message({
              message: param.titleName+'删除成功~',
              type: 'success'
            });
          }
        },

        // 清空tag
        deleteTagList(){
          this.tagItems=this.tagItems.filter(item=>item.tagId.length===1);
          this.$message({
            message: 'tag清空成功~',
            type: 'success'
          });
          this.tagList=this.tagList.splice(0,5);
          chrome.storage.sync.set({ 'tagList': this.tagItems });
        },

        // 删除单个tag
        deleteTag(param){
          if(param.tagId.length===1){
            this.$message({
              message: 'Pet原始提示词不能删除哦~',
              type: 'warning'
            });
          }else {
            chrome.storage.sync.get(['tagList'], (result) => {
              if(result.tagList&&result.tagList.length>0){
                this.tagItems=result.tagList.filter(item=>item.tagId!==param.tagId);
                chrome.storage.sync.set({ 'tagList': this.tagItems });
                let deleteTagFlag=true;
                for(let i=0;i<this.tagItems.length;i++){
                  if(this.tagItems[i].tagType===param.tagType){
                    deleteTagFlag=false;
                    break;
                  }
                }
                if(deleteTagFlag){
                  this.tagList=this.tagList.filter(item=>item.tagName!==param.tagType);
                  chrome.storage.sync.set({ 'tagTypeList': this.tagList });
                }
                this.searchTagType=0;
                this.searchContent='';
                this.$message({
                  message: '提示词'+param.tagName+'清除成功~',
                  type: 'success'
                });
              }
            });
          }
        },

        // 下载所有的Tag
        downloadTag(){
          let tagContent=this.tagItems;
          let downLoadContent='';
          for(let i=0;i<tagContent.length;i++){
            downLoadContent+='Tag类别：'+tagContent[i].tagType+'\n';
            downLoadContent+='Tag名称：'+tagContent[i].tagName+'\n';
            downLoadContent+='Tag内容:'+tagContent[i].tagContent +'\n';
            downLoadContent+='===========================================================================\n\n';
          }
          let date = new Date();
          let year = date.getFullYear();
          let month = date.getMonth() + 1;
          let strDate = date.getDate();
          let hour = date.getHours(); // 时
          let minutes = date.getMinutes(); // 分
          let seconds = date.getSeconds() //秒
          if (month >= 1 && month <= 9) {
            month = "0" + month;
          }
          if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
          }
          let currentdate = year + month + strDate+ hour+minutes+seconds;
          let name='Tag列表下载'+'('+currentdate+')';
          download(downLoadContent,name+'.txt');
          this.$message({
            type: 'success',
            message: 'Tag列表内容下载成功~'
          });
        },

        // 创建新tag
        createTag(){
          this.colorSelectFlag=false;
          this.tempColor='';
          this.inputCreateContent='';
          this.tagClassFlag=true;
          this.tempTagId='';
          this.tagShowType=1;
          this.addTagType=0;
          this.selectCreateContent='';
          this.promptTitle='';
          this.promptContent='';
          this.tempTagList=this.tagList.slice(1);
        },

        // 编辑tag
        editTag(param){
          this.tempTagId=param.tagId;
          this.colorSelectFlag=true;
          this.tempColor=param.color;
          this.tagClassFlag=true;
          this.inputCreateContent=param.tagType.substring(1);
          this.tagShowType=1;
          this.addTagType=1;
          this.tempTagList=this.tagList.slice(1);
          this.selectCreateContent=param.label;
          this.promptTitle=param.tagName;
          this.promptContent=param.tagContent;
        },

        // 添加tag信息
        addTag(){
          if(!this.selectCreateContent&&!this.inputCreateContent&&!this.tagClassFlag){
            this.$message({
              message: 'Prompt类不能为空哦~',
              type: 'warning'
            });
            return;
          }
          if(!this.promptTitle){
            this.$message({
              message: 'Prompt标题不能为空哦~',
              type: 'warning'
            });
            return;
          }
          if(!this.promptContent){
            this.$message({
              message: 'Prompt内容不能为空哦~',
              type: 'warning'
            });
            return;
          }
          let tagTemp={};
          if(this.tempTagId){
            tagTemp.tagId=this.tempTagId;
          }else{
            tagTemp.tagId=this.getUuid();
          }
          if(this.tagClassFlag){
            tagTemp.tagType=this.tagList[this.selectCreateContent].tagName;
            tagTemp.color=this.tagList[this.selectCreateContent].tagColor;
            tagTemp.label=this.selectCreateContent;
          }else{
            tagTemp.tagType='#'+this.inputCreateContent;
            tagTemp.color=this.tempColor?this.tempColor:'#630876';
            tagTemp.label=this.tagList.length;
            let tagTypeTemp={};
            tagTypeTemp.tagName=tagTemp.tagType;
            tagTypeTemp.tagColor=tagTemp.color;
            tagTypeTemp.label=this.tagList.length;
            this.tagList.push(tagTypeTemp);
            chrome.storage.sync.set({ 'tagTypeList': this.tagList });
          }
          tagTemp.tagName=this.promptTitle;
          tagTemp.tagContent=this.promptContent;
          if(this.addTagType===1){
            for(let i=0;i<this.tagItems.length;i++){
              if(this.tagItems[i].tagId===this.tempTagId){
                this.tagItems[i]=tagTemp;
              }
            }
          }else{
            this.tagItems.push(tagTemp);
          }
          chrome.storage.sync.set({ 'tagList': this.tagItems });
          this.tagShowType=0;
          this.$message({
            message: this.addTagType===1?'提示词'+tagTemp.tagName+'修改成功~':'提示词'+tagTemp.tagName+'添加成功~',
            type: 'success'
          });
        },

        // 清空tag的信息
        deleteTemp(){
          if(this.addTagType===0){
            this.selectCreateContent='';
            this.inputCreateContent='';
          }
          this.promptTitle='';
          this.promptContent='';
        },

        // 使用Tag
        useTag(param){
          this.$copyText(param.tagContent).then(
              e=>{
                this.$message({
                  message: 'prompt复制成功,快去使用吧~',
                  type: 'success'
                });
              },
              e=>{
                this.$message.error('复制失败');
              }
          )
        },

        // 关闭窗口
        closeTag(){
          this.tagShowType=0;
        },


        changeShow(){
          chrome.storage.sync.set({ 'showType': this.showPetPluginSwitch });
          this.reloadTab();
        },
        reSet(){
          this.fontSize=15;
          this.colorType='#3D4554';
          this.radioValue='1';
          chrome.storage.sync.set({ 'fontSize': this.fontSize});
          chrome.storage.sync.set({ 'colorType': this.colorType});
          chrome.storage.sync.set({ 'radioValue': this.radioValue});
          this.reloadTab();
        },
        reloadTab(){
          chrome.tabs.query({ active: true, currentWindow: true }, function(tabs) {
            var activeTab = tabs[0];
            //在这里可以对当前激活的标签页进行操作
            chrome.tabs.reload(activeTab.id);
          });
        },
        initInfo(){
          chrome.storage.sync.get(['showType','fontSize','colorType','radioValue'], (result) => {
            //初始化字体大小
            if(result.fontSize){
              this.fontSize=result.fontSize;
            }else {
              this.fontSize=15;
              chrome.storage.sync.set({'fontSize': this.fontSize});
            }

            //初始化颜色
            if(result.colorType){
              this.colorType=result.colorType;
            }else {
              this.colorType='#3D4554';
              chrome.storage.sync.set({'colorType': this.colorType});
            }

            //初始化AI内核
            if(result.radioValue){
              this.radioValue=result.radioValue;
            }else {
              this.radioValue='1';
              chrome.storage.sync.set({'radioValue': this.radioValue});
            }

            //初始化Switch
            if(result.showType){
              this.showPetPluginSwitch=result.showType;
            }else{
              chrome.storage.sync.set({ 'showType': false }, () => {
                this.showPetPluginSwitch=false
              });
            }
          });
        },
        closePlugin(){
          chrome.storage.sync.set({ 'showType': false });
          chrome.storage.sync.set({ 'loginType': '0' });
          location.reload();
          this.reloadTab();
        },
        changeSelectType(param){
          for(let i=0;i<this.buttonTypeList.length;i++){
            this.buttonTypeList[i].type='';
          }
          param.type='primary';
          this.selectType=param.select;
        },
        moreSetting(){
          window.open(chrome.runtime.getURL("options.html"));
        },
        goToChatPet(){
          window.open('http://www.chatpet.top');
        },
      },
      watch:{
        fontSize(newVal,oldVal){
          if(!newVal){
            newVal='#3D4554';
            this.fontSize='#3D4554';
          }
          chrome.storage.sync.set({'fontSize': newVal});
          this.reloadTab();
        },
        colorType(newVal,oldVal){
          chrome.storage.sync.set({'colorType': newVal});
          this.reloadTab();
        },
        radioValue(newVal,oldVal){
          chrome.storage.sync.set({'radioValue': newVal});
          this.reloadTab();
        },
        searchContent(newVal,oldVal){
          chrome.storage.sync.get('chatPluginList', (result) => {
            this.chatPluginList=[]
            if(result.chatPluginList&&result.chatPluginList.length>0){
              if(newVal){
                for(let i=0;i<result.chatPluginList.length;i++){
                  if(result.chatPluginList[i].titleName.includes(newVal)){
                    this.chatPluginList.push(result.chatPluginList[i])
                  }
                }
              }else {
                this.chatPluginList=result.chatPluginList;
              }
            }
          });
        },
        inputCreateContent(newVal,oldVal){
          if(!this.tagClassFlag){
            for(let i=0;i<this.tagList.length;i++){
              if(this.tagList[i].tagName.substring(1)===newVal){
                this.$message({
                  message: '列表中已有此类别，请前往列表中选择~',
                  type: 'warning'
                });
                this.inputCreateContent='';
              }
            }
          }
        },
        tagItems(newVal,oldVal){
          this.tagItems=newVal;
        },
        tagList(newVal,oldVal){
          this.tagList=newVal;
        },
        searchTagType(newVal,oldVal){
          if(newVal!==''){
            chrome.storage.sync.get(['tagTypeList','tagList'], (result) => {
              if(!result.tagTypeList||result.tagTypeList.length===0){
                chrome.storage.sync.set({ 'tagTypeList': this.tagList });
              }else {
                this.tagItems=result.tagList;
                if(newVal!==0){
                  this.tagList=result.tagTypeList;
                  this.tagItems=this.tagItems.filter(item=>item.label==newVal);
                  this.tagItems=this.tagItems.filter(item=>item.tagName.includes(this.tagSearchContent));
                }
              }
            });
          }else {
            this.initTagList();
          }
        },
        tagSearchContent(newVal,oldVal){
          chrome.storage.sync.get(['tagTypeList','tagList'], (result) => {
            if(newVal){
              if(!result.tagList||result.tagList.length===0){
                chrome.storage.sync.set({ 'tagList': this.tagItems });
              }else {
                this.tagItems=result.tagList;
                if(this.searchTagType!==0){
                  this.tagItems=this.tagItems.filter(item=>item.label===this.searchTagType);
                }
                this.tagItems=this.tagItems.filter(item=>item.tagName.includes(newVal));
              }
            }else {
              this.initTagList();
            }
          });
        },
      }
    }
</script>

